import React from 'react'
import "./css/priceDetails.css"
import { FormatPrice } from '../../../services/formatPrice'

const PriceDetails = () => {
  return (
    <React.Fragment>
        <div className="priceDetails-container">
            <p className='priceDetails-heading' >PRICE DETAILS</p>
            <table className='priceDetails-table'>
                <tr>
                    <td>Price (4 items)</td>
                    <td><FormatPrice price={20000}/></td>
                </tr>
                <tr>
                    <td>Delivery Charges</td>
                    <td><FormatPrice price={60}/></td>
                </tr>
                <tr>
                    <td>Packing Charge</td>
                    <td><FormatPrice price={40}/></td>
                </tr>
                <tr>
                    <th style={{color: '#017001'}}>Total Payable</th>
                    <th style={{color: '#017001'}}><FormatPrice price={20100}/></th>
                </tr>
            </table>
            <p className='priceDetails-totalSaving'>Your Total savings on this order <FormatPrice price={2850}/></p>
            <button className='priceDetails-confirmBtn'>
                Confirm the purchase
            </button>
        </div>
    </React.Fragment>
  )
}

export default PriceDetails